<script>
  export let active = false;
  export let text = "Button";

  export let onClick = select;

  export let big = false;

  function select() {
    return !active;
  }
</script>


<style>
  .toggle-btn {
    border-radius: 0px;
    border: 0px;
    padding: 0.4rem;
    cursor: pointer;
  }
  .toggle-btn.big {
    width: 50px;
    height: 50px;
  }
  .toggle-btn.active {
    background-color: #60b160;
  }
  .toggle-btn.active:hover {
    background-color: #4c8a4c;
  }
</style>

<button 
  class="toggle-btn"
  class:big={big} 
  class:active={active}
  on:click|preventDefault={() => active = onClick()}
>
  {text}
</button>