<script context="module">
  import ItemMeta from "./ItemMeta.svelte";
  export const types = ["fullList", "cashier", "order"];

  const fields = {
    fullList: {
      fields: {
        orderId: ["order", "id"],
        firstName: ["order", "shipping", "first_name"],
        lastName: ["order", "shipping", "last_name"],
        billingFirstName: ["order", "billing", "first_name"],
        billingLastName: ["order", "billing", "last_name"],
        item: ["item", "name"],
        itemMeta: { p: ["item", "meta_data"], component: ItemMeta }
      }
    }
  };

  function sortOrders(o1, o2) {
    const o1key =
      o1.shipping.last_name +
      " " +
      o1.shipping.first_name +
      " " +
      o1.billing.last_name +
      " " +
      o1.billing.first_name;
    const o2key =
      o2.shipping.last_name +
      " " +
      o2.shipping.first_name +
      " " +
      o2.billing.last_name +
      " " +
      o2.billing.first_name;
    if (o1 < o2) return -1;
    if (o2 < o1) return 1;
    return 0;
  }
</script>

<script>
  import {get} from "underscore"
  import { orders } from "../../store/data.js";

  export let type = null;

  let list;
  $: list = getData();

  function getData() {
    if (type === "fullList") {
      return $orders.orders.sort(sortOrders).flatMap(o => o.line_items.map(itm => ({order: o, item: itm})))
    }
  }
</script>
<svelte:options accessors />

<table>
<thead>
<tr>
{#each Object.keys(fields[type]?.fields??{}) as f}
  <th>
    {f}
  </th>
{/each}
</tr>
</thead>
<tbody>
{#each list ?? [] as l}
<tr>
{#each Object.keys(fields[type]?.fields??{}) as f}
  <td>
  {#if Array.isArray(fields[type]?.fields[f])}
    {get(l,fields[type]?.fields[f])}
  {:else}
    <svelte:component this={fields[type]?.fields[f].component} data={get(l, fields[type]?.fields[f]?.p)} /> 
  {/if}
  </td>
{/each}
</tr>
{/each}
</tbody>
</table>

<pre>
{Array.isArray([])}
{JSON.stringify(list, null, 2)}
</pre>