import { parseLinkHeader } from "./parseLinkHeader.js";

function getUrl(path, connection) {
  return `https://${connection.host}/${path}?consumer_key=${connection.key}&consumer_secret=${connection.secret}`;
}

export async function checkConnection(connection) {
  const res = await fetch(getUrl("wp-json/wc/v2/system_status", connection));
  if (res.status !== 200) {
    throw new Error(
      `Status Code: ${res.status}\n${JSON.stringify(await res.json(), null, 2)}`
    );
  }
  return await res.json();
}

export async function getOrders(connection) {
  const orders = [];

  let url = getUrl("wp-json/wc/v2/orders", connection);
  do {
    console.log("loading", url);

    const res = await fetch(url);
    url = parseLinkHeader(res.headers.get("Link"))?.next;
    orders.push(...(await res.json()));
  } while (url != null);

  return orders;
}

function getOrdersProducts(orders) {
  return Object.values(
    orders
      .flatMap((o) => o.line_items)
      .map((o) => ({
        product_id: o.product_id,
        variation_id: o.variation_id
      }))
      .reduce(
        (prev, cur) => ({
          ...prev,
          [`${cur.product_id}_${cur.variation_id}`]: cur
        }),
        {}
      )
  );
}

async function getProductImgUrl(product, connection) {
  const url = getUrl(
    product.variation_id
      ? `wp-json/wc/v2/products/${product.product_id}/variations/${product.variation_id}`
      : `wp-json/wc/v2/products/${product.product_id}`,
    connection
  );
  console.log(url);

  return fetch(url)
    .then((r) => r.json())
    .then((j) => j?.image?.src || j?.images?.[0]?.src);
}

export async function getProductImages(orders, connection) {
  const products = getOrdersProducts(orders);

  return Promise.all(
    products.map(async (p) => ({
      ...p,
      img: await getProductImgUrl(p, connection)
    }))
  ).then((p) =>
    p.reduce(
      (prev, cur) => ({
        ...prev,
        [`${cur.product_id}_${cur.variation_id}`]: cur
      }),
      {}
    )
  );
}
