<script>
		import { connection } from "../store/connection.js";
		import { checkConnection } from "../lib/api.js";
		import { reloadData } from "../store/data.js";

		let error;
		let loading = false;

		async function reload() {
		  error = null;
		  loading = true;
		  try {
		    console.log(await checkConnection($connection));
		  } catch (err) {
		    console.log(err);
		    error = err;
		  }
		  if (!error) {
		    await reloadData($connection);
		    loading = false;
		  }
		}
</script>

<form> 
	<input name="host" type="text" bind:value={$connection.host}/>
	<input name="key" type="text" bind:value={$connection.key}/>
	<input name="secret" type="password" bind:value={$connection.secret}/>
	<button on:click|preventDefault={reload}>Reload</button>
	{#if loading}
	Loading...
	{/if}
</form>

{#if error}
<pre>
{error}
</pre>
{/if}