<script>
  import {
    orders,
    products,
    itemsData,
    selectedOrder,
    orderState
  } from "../store/data.js";

  import ToggleBtn from "./ToggleBtn.svelte";

  $: order = $selectedOrder;
  $: email = order && (order.shipping.email || order.billing.email);
  $: status = $orderState[order && order.id];

  function toggleStatus(item, status) {
    console.log("Toggle", item, status);

    itemsData.update(i => {
      if (status === "done" && !i[item]?.done) {
        return {
          ...i,
          [item]: { done: true, inStock: true, paid: true }
        };
      }
      const newState = !(i[item]?.[status] ?? false);
      const donestate = newState ? {} : { done: false };
      return {
        ...i,
        [item]: { ...i[item], [status]: newState, ...donestate }
      };
    });
  }

  function toggleOrderStatus(st) {
    const current = status[st];

    let donestate = {};

    if (!current) {
      if (st === "done") {
        donestate = { paid: true, inStock: true };
      }
    } else {
      if (st !== "done") {
        donestate = { done: false };
      }
    }

    itemsData.update(i => {
      const newStates = order.line_items
        .map(it => ({ ...i[it.id], id: it.id, [st]: !current }))
        .reduce(
          (p, c) => ({ ...p, [c.id]: { ...c, ...donestate, id: undefined } }),
          {}
        );
      return { ...i, ...newStates };
    });
  }
</script>




<style>
  .order-details {
    display: flex;
    justify-content: space-between;
  }
  .order-state {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    margin: 1rem 0rem;
  }
  .items-list {
    padding: 0px;
  }
  .product_img {
    display: block;
    margin-left: 1rem;
    margin-right: 0px;
    margin-top: auto;
    margin-bottom: auto;
    height: 125px;
    max-width: 150px;
  }
  .product_list_item {
    display: block;
    margin: 0rem;
    padding: 0.5rem 0rem;
    background-color: mistyrose;
  }
  .product_list_item.in-stock {
    background-color: lemonchiffon;
  }
  .product_list_item.paid {
    background-color: peachpuff;
  }
  .product_list_item.paid.in-stock {
    background-color: #d0fdd0;
  }
  .product_list_item.done {
    background-color: #d0fdd0;
    opacity: 0.5;
  }
  .product {
    display: flex;
    gap: 1rem;
    align-items: stretch;
    justify-content: space-between;
  }
  .product_details {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .product_details > p {
    margin: 0.25rem;
  }
  .product_status {
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items: stretch;
  }
  .bold {
    font-weight: bold;
  }
</style>



<main style="flex-grow: 2">
{#if order}
  <div class="order-details">
    <div>
      <h2>Bestellung {order.shipping.first_name} {order.shipping.last_name}</h2>
      <p>{order.billing.first_name} {order.billing.last_name}</p>
      <p><a href={`mailto:${email}?subject=${encodeURIComponent(`Bestellung ScBirs Shop #${order.id}`)}`}>{email}</a></p>
      <p>Status: {order.status}</p>
      <p class="bold">Price: {order.currency_symbol} {order.total}</p>
      {#if order.customer_note}
      <p>
        {order.customer_note}
      </p>
      {/if}
    </div>
    <div class="order-state">
      <ToggleBtn text="Paid" big={true} onClick={() => toggleOrderStatus("paid")} active={status.paid} />
      <ToggleBtn text="In Stock" big={true} onClick={() => toggleOrderStatus("inStock")} active={status.inStock} />
      <ToggleBtn text="Done" big={true} onClick={() => toggleOrderStatus("done")} active={status.done} />
    </div>
  </div>


  <ul class="items-list">
    {#each order.line_items as item}
      <li class="product_list_item" 
        class:paid={$itemsData[item.id]?.paid}
        class:in-stock={$itemsData[item.id]?.inStock}
        class:done={$itemsData[item.id]?.done}
      >
        <article class="product">
          <img class="product_img" src={$products[`${item.product_id}_${item.variation_id}`].img} alt={`Product ${item.name}`}/>
          <div class="product_details">
            <h3>{item.quantity}x {item.name}</h3>
            <p>{order.currency_symbol} {item.subtotal}</p>
            <p>{item.sku}</p>
            {#if item.meta_data.length}
              <ul class="bold">
                {#each item.meta_data as meta}
                  <li>
                    {meta.display_key}: {meta.display_value}
                  </li>
                {/each}
              </ul>
            {/if}
          </div>
          <div class="product_status">
            <ToggleBtn text="Paid" big={true} onClick={() => toggleStatus(item.id, "paid")} active={$itemsData[item.id]?.paid} />
            <ToggleBtn text="In Stock" big={true} onClick={() => toggleStatus(item.id, "inStock")} active={$itemsData[item.id]?.inStock} />
            <ToggleBtn text="Done" big={true} onClick={() => toggleStatus(item.id, "done")} active={$itemsData[item.id]?.done} />
          </div>
        </article>
      </li>
    {/each}
  </ul>
{/if}
</main>
