import { storage } from "../lib/localstorage.js";
import { getOrders, getProductImages } from "../lib/api.js";
import { derived } from "svelte/store";

export const orders = storage("orders", { selected: null, orders: [] });

export const products = storage("products", {});

export const itemsData = storage("local-items-data", {});

export const selectedOrder = derived(orders, (orders) =>
  orders?.orders?.find((o) => o.id === orders.selected)
);

export const orderState = derived([orders, itemsData], ([orders, itemsData]) =>
  orders.orders
    .map((order) => ({
      id: order.id,
      state: order.line_items
        .map((item) => ({
          ...itemsData[item.id]
        }))
        .reduce(
          (p, c) => ({
            paid: p.paid && !!c.paid,
            inStock: p.inStock && !!c.inStock,
            done: p.done && !!c.done
          }),
          { paid: true, inStock: true, done: true }
        )
    }))
    .reduce((p, c) => ({ ...p, [c.id]: c.state }), {})
);

export async function reloadData(connection) {
  const newOrders = await getOrders(connection);
  orders.update((u) => ({ ...u, orders: newOrders }));

  const newProducts = await getProductImages(newOrders, connection);
  console.log({ newProducts });

  products.update((u) => newProducts);
}
