<script>
  export let status = {};
</script>


<style>
  .status-icons {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 4px;
    width: 2rem;
    align-items: center;
  }
  .status-icons > img,
  .status-icons > p {
    width: 16px;
    height: 16px;
    margin: 0px;
    padding: 0px;
  }
</style>


<div class="status-icons">

{#if status.paid}
  <img src="img/paper-bill.png" alt="Order Paid Icon"/>
{:else} 
<p></p>
{/if}
{#if status.inStock}
  <img src="img/truck.png" alt="Order Paid Icon"/>
  {:else} 
<p></p>
{/if}
{#if status.done}
  <img src="img/check-mark.png" alt="Order Paid Icon"/>
  {:else} 
<p></p>
{/if}


</div>