<script>
  import OrderFilter from "./OrderFilter.svelte";
  import { orders, orderState } from "../store/data.js";
  import { recursiveObjectValues } from "../lib/util.js";

  import StatusIcons from "./StatusIcons.svelte";

  function selectOrder(order) {
    orders.update(o => ({ ...o, selected: order.id }));
  }

  const statusFilter = ["completed", "cancelled", "refunded", "failed"];

  let searchFilter = "";
  let prodStatusFilter = {
    paid: false,
    inStock: false,
    done: false
  };

  $: filteredOrders = $orders.orders
    .filter(o => !statusFilter.includes(o.status))
    .filter(
      o =>
        ($orderState[o.id].paid || !prodStatusFilter.paid) &&
        ($orderState[o.id].inStock || !prodStatusFilter.inStock) &&
        ($orderState[o.id].done || !prodStatusFilter.done)
    )
    .filter(o =>
      JSON.stringify(recursiveObjectValues(o))
        .toLowerCase()
        .includes(searchFilter.toLowerCase())
    );
</script>

<style>
  .orders-list {
    margin: 2rem 0rem;
    height: 100%;
  }
  .orders-list > ol {
    list-style: none;
    padding: 0px;
    margin: 0.25rem;
  }
  .list-item {
    width: 100%;
    border-radius: 0px;
    border: 0px;
    background-color: transparent;
    text-align: left;
    cursor: pointer;
    display: flex;
    gap: 0.25rem;
    padding: 0.5rem 0rem;
  }
  .list-item:hover,
  .list-item.active {
    background-color: #d5f5e3;
  }
  .order-recv {
    display: flex;
    flex-direction: column;
    gap: 0px;
  }
  .order-shipping {
    font-weight: bold;
    margin: 0px;
  }
  .order-payment {
    font-style: italic;
    margin: 0px;
  }
</style>

<nav class="orders-list">
<OrderFilter bind:search={searchFilter} bind:filter={prodStatusFilter} />
{#if $orders.orders}
<ol>
{#each filteredOrders as order, i}
  <li>
    <button class="list-item" class:active={$orders.selected === order.id} on:click={() => selectOrder(order)} >
    <StatusIcons status={$orderState[order.id]}/>    
    <div class="order-recv">
      <p class="order-shipping">
        {order.shipping.first_name}
        {order.shipping.last_name}
      </p>
      <p class="order-payment">
        {order.billing.first_name}
        {order.billing.last_name}
      <p>
    </div>
  </button>
  </li>
{/each}
</ol>
{/if}
</nav>